
import React, { Component } from 'react';
import { connect } from "react-redux";
import { Dropdown, Accordion, Nav, Tab, ToggleButtonGroup, ToggleButton, OverlayTrigger, Tooltip } from 'react-bootstrap';
import { baseUrl } from '../../../../../Assets/AssetsHelper';
import CreatableSelect from 'react-select/creatable';
import AndOrCondition from './AndOr'

class Tags extends Component {
    state = {
        tagString: [],
        newTag: "",
        listTags: [],
        selectedOption: null
    };

    onClickToRemoveTag = (index) => event => {
        let { tagString } = this.state;
        tagString.splice(index, 1)
        this.setState({ tagString })

    }
    componentWillMount() {
        const { tagList } = this.props
        if (tagList && tagList.length > 0) {
            var listTags = []
            tagList.map((item) => {
                listTags.push({ value: item, label: item })
            })
            this.setState({ listTags })


        }

    }
    componentDidMount() {
        const { modalAssetData } = this.props
        if (modalAssetData && Object.keys(modalAssetData).length > 0) {
            if (modalAssetData.tags && String(modalAssetData.tags).length > 0) this.setState({ tagString: String(modalAssetData.tags).split(",") })
        }
    }

    onClickNewAddTag = event => {
        const { newTag, tagString } = this.state;
        const { modalAssetData } = this.props
        if (newTag && String(newTag).length >= 3) {
            tagString.push(newTag)
            this.setState({ tagString, newTag: "", selectedOption: null })
        }
        else {

        }
    }
    addNewTag = event => this.setState({ newTag: event.target.value })


    setNewTags = (selectedOption) => {
        if (selectedOption.value) {
            this.setState({ newTag: "", selectedOption: null }, () => this.props.onTagApply(selectedOption.value))
        }
    };

    render() {
        const { tagString, newTag, listTags, selectedOption } = this.state
        const { state, onTagRemove, conditionApply, filterApply, filterKey, indexFilter,isLast,onSelectActive } = this.props

        return  <Accordion activeKey={state.activeKey} onSelect={onSelectActive(filterKey)?onSelectActive(filterKey):null}>
                    <Accordion.Item eventKey={'tags'} className="pb-2">
                    <div className="d-flex">
                        <Accordion.Header className="me-1">
                            <div className="d-flex flex-grow-1 align-items-center pe-4">
                                <span className="svg-icon svg-icon-4 text-gray-800">
                                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                        <path fill="none" d="M0 0h24v24H0z" />
                                        <path d="M10.9 2.1l9.899 1.415 1.414 9.9-9.192 9.192a1 1 0 0 1-1.414 0l-9.9-9.9a1 1 0 0 1 0-1.414L10.9 2.1zm.707 2.122L3.828 12l8.486 8.485 7.778-7.778-1.06-7.425-7.425-1.06zm2.12 6.364a2 2 0 1 1 2.83-2.829 2 2 0 0 1-2.83 2.829z" fill="currentColor" />
                                    </svg>
                                </span>
                                <div className="fs-8 text-gray-800 fw-semibold">Tags</div>
                            </div>
                            <button type="button" className="close_filter btn btn-icon position-absolute end-0" onClick={filterApply(filterKey)}></button>
                        </Accordion.Header>
                        <AndOrCondition condition={state.condition} conditionApply={conditionApply} />
                    </div>
                    <Accordion.Body className="border rounded-1 mt-1">
                        {state.feedData && Object.keys(state.feedData).length > 0 ?
                        <>
                        <div className="form-group mb-2">
                            <CreatableSelect isClearable options={listTags} onChange={this.setNewTags} value={selectedOption} />
                        </div>
                        <div className="tags-group tags-group-sm d-flex align-items-center flex-wrap">
                            {
                                state.tagApply ? state.tagApply.map((tagItem, tagKey) => <div className="block-center badge bg-secondary text-gray-800 rounded-pill border me-1 mb-1">
                                    {tagItem}
                                    <button className="btn p-0 ms-1" onClick={onTagRemove(tagKey)}>
                                        <span className="svg-icon me-0">
                                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                                <path fill="none" d="M0 0h24v24H0z" />
                                                <path d="M12 10.586l4.95-4.95 1.414 1.414-4.95 4.95 4.95 4.95-1.414 1.414-4.95-4.95-4.95 4.95-1.414-1.414 4.95-4.95-4.95-4.95L7.05 5.636z" />
                                            </svg>
                                        </span>
                                    </button>
                                </div>) : null
                            }
                        </div>
                        </>
                        :
                        <div className="py-4">
                            <h6 className="text-center fs-8 mb-0">No Tags Available</h6>
                        </div>
                        }
                    </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
    }
}
const mapStateToProps = (state) => {
    return {
        tagList: state.tags.tagList
    }
}

export default connect(mapStateToProps)(Tags);
