
import React, { Component } from 'react';
import { Accordion } from 'react-bootstrap';
import AndOrCondition from './AndOr'
class AssetsTypes extends Component {
    render() {

        const { onAssetsApply, onSelectAllAssetsApply, state, conditionApply, filterApply, filterKey, indexFilter,isLast,onSelectActive } = this.props
        return <Accordion activeKey={state.activeKey} onSelect={onSelectActive(filterKey)?onSelectActive(filterKey):null}>
                    <Accordion.Item eventKey={'assetsType'} className="pb-2">
                    <div className="d-flex">
                        <Accordion.Header className="me-1">
                            <div className="d-flex flex-grow-1 align-items-center pe-4">
                                <span className="svg-icon svg-icon-4 text-gray-800">
                                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                        <path fill="none" d="M0 0h24v24H0z" />
                                        <path d="M4.828 21l-.02.02-.021-.02H2.992A.993.993 0 0 1 2 20.007V3.993A1 1 0 0 1 2.992 3h18.016c.548 0 .992.445.992.993v16.014a1 1 0 0 1-.992.993H4.828zM20 15V5H4v14L14 9l6 6zm0 2.828l-6-6L6.828 19H20v-1.172zM8 11a2 2 0 1 1 0-4 2 2 0 0 1 0 4z" fill="currentColor" />
                                    </svg>
                                </span>
                                <div className="fs-8 text-gray-800 fw-semibold">Assets Type</div>
                            </div>
                            <button type="button" className="close_filter btn btn-icon position-absolute end-0" onClick={filterApply(filterKey)}></button>
                        </Accordion.Header>
                        <AndOrCondition condition={state.condition} conditionApply={conditionApply} />
                    </div>
                    <Accordion.Body className="border rounded-1 mt-1">
                        <div className="navi-item d-flex align-items-center border-bottom pt-2 pb-3 cursor-pointer">
                            <label className="form-check flex-grow-1" htmlFor="selectAllassets">
                                <div className="navi-text">
                                    <p className="fs-8 text-gray-800 fw-semibold mb-0">Select All</p>
                                </div>
                            </label>
                            <input className="form-check-input" type="checkbox" name="" value="phone" id="selectAllassets" checked={Object.keys(state.assetsApply).length >= 3 ? true : false} onClick={onSelectAllAssetsApply} />
                        </div>
                        <div className="navi-item d-flex align-items-center border-bottom py-2 cursor-pointer cursor-pointer">
                            <label className="form-check flex-grow-1 align-items-center" htmlFor="image_assets">
                                <div className="symbol symbol-30 bg-light me-2">
                                    <div className="symbol-label">
                                        <span className="svg-icon svg-icon-5 text-gray-800 me-0">
                                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                                <path fill="none" d="M0 0h24v24H0z" />
                                                <path d="M4.828 21l-.02.02-.021-.02H2.992A.993.993 0 0 1 2 20.007V3.993A1 1 0 0 1 2.992 3h18.016c.548 0 .992.445.992.993v16.014a1 1 0 0 1-.992.993H4.828zM20 15V5H4v14L14 9l6 6zm0 2.828l-6-6L6.828 19H20v-1.172zM8 11a2 2 0 1 1 0-4 2 2 0 0 1 0 4z" fill="currentColor" />
                                            </svg>
                                        </span>
                                    </div>
                                </div>
                                <div className="navi-text">
                                    <p className="fs-8 text-gray-800 fw-semibold mb-0">Image Assets</p>
                                </div>
                            </label>
                            <input className="form-check-input" type="checkbox" name="" value="phone" id="image_assets" checked={Object.keys(state.assetsApply).includes("2,4") ? true : false} onClick={onAssetsApply("2,4")} />
                        </div>
                        <div className="navi-item d-flex align-items-center border-bottom py-2 cursor-pointer cursor-pointer">
                            <label className="form-check flex-grow-1 align-items-center" htmlFor="video_assets">
                                <div className="symbol symbol-30 bg-light me-2">
                                    <div className="symbol-label">
                                        <span className="svg-icon svg-icon-5 text-gray-800 me-0">
                                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                                <path fill="none" d="M0 0h24v24H0z" />
                                                <path d="M2 3.993A1 1 0 0 1 2.992 3h18.016c.548 0 .992.445.992.993v16.014a1 1 0 0 1-.992.993H2.992A.993.993 0 0 1 2 20.007V3.993zM4 5v14h16V5H4zm6.622 3.415l4.879 3.252a.4.4 0 0 1 0 .666l-4.88 3.252a.4.4 0 0 1-.621-.332V8.747a.4.4 0 0 1 .622-.332z" fill="currentColor" />
                                            </svg>
                                        </span>
                                    </div>
                                </div>
                                <div className="navi-text">
                                    <p className="fs-8 text-gray-800 fw-semibold mb-0">Video Assets</p>
                                </div>
                            </label>
                            <input className="form-check-input" type="checkbox" name="" value="phone" id="video_assets" checked={Object.keys(state.assetsApply).includes("3,5") ? true : false} onClick={onAssetsApply("3,5")} />
                        </div>
                        <div className="navi-item d-flex align-items-center py-2 cursor-pointer">
                            <label className="form-check flex-grow-1 align-items-center" htmlFor="text_assets">
                                <div className="symbol symbol-30 bg-light me-2">
                                    <div className="symbol-label">
                                        <span className="svg-icon svg-icon-5 text-gray-800 me-0">
                                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                                <path fill="none" d="M0 0h24v24H0z" />
                                                <path d="M13 6v15h-2V6H5V4h14v2z" fill="currentColor" />
                                            </svg>
                                        </span>
                                    </div>
                                </div>
                                <div className="navi-text">
                                    <p className="fs-8 text-gray-800 fw-semibold mb-0">Text Assets</p>
                                </div>
                            </label>
                            <input className="form-check-input" type="checkbox" name="" value="phone" id="text_assets" checked={Object.keys(state.assetsApply).includes("1") ? true : false} onClick={onAssetsApply("1")} />
                        </div>
                    </Accordion.Body>
                </Accordion.Item>
                </Accordion>
    }
}

export default AssetsTypes