
import React, { Component } from 'react';
import { Accordion } from 'react-bootstrap';
import { baseUrl } from '../../../../../Assets/AssetsHelper';
import { GET_NETWORK_ICON } from '../../../../../Services/Utils/utils';
import AndOrCondition from './AndOr'
class Network extends Component {


    render() {
        const { state, onNetworkApply, onClickSelectAll, onSearchFilter, filterApply, filterKey, conditionApply, indexFilter,isLast,onSelectActive } = this.props

        return <Accordion activeKey={state.activeKey} onSelect={onSelectActive(filterKey)?onSelectActive(filterKey):null}>
                <Accordion.Item eventKey={'source'} className="pb-2">
                    <div className="d-flex">
                        <Accordion.Header className="me-1">
                            <div className="d-flex flex-grow-1 align-items-center pe-4">
                                <span className="svg-icon svg-icon-4 text-gray-800">
                                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                        <path id="Path_1" data-name="Path 1" d="M0,0H24V24H0Z" fill="none" />
                                        <path id="Path_4" data-name="Path 4" d="M8.365,20.286A9.309,9.309,0,0,1,2.9,14.82a3.246,3.246,0,0,1,0-6.455A9.309,9.309,0,0,1,8.365,2.9a3.246,3.246,0,0,1,6.455,0,9.309,9.309,0,0,1,5.465,5.466,3.246,3.246,0,0,1,0,6.455,9.311,9.311,0,0,1-5.466,5.465,3.246,3.246,0,0,1-6.455,0Zm1.836-.347a1.391,1.391,0,0,0,2.782.048v-.1a1.391,1.391,0,0,0-2.782.048ZM6.328,16.818a7.393,7.393,0,0,0,2.388,1.614,3.247,3.247,0,0,1,5.751,0,7.444,7.444,0,0,0,3.965-3.963,3.247,3.247,0,0,1,0-5.752,7.447,7.447,0,0,0-3.964-3.964,3.247,3.247,0,0,1-5.752,0A7.444,7.444,0,0,0,4.752,8.716a3.247,3.247,0,0,1,0,5.752A7.427,7.427,0,0,0,6.328,16.818Zm12.22-5.226a1.391,1.391,0,1,0,1.39-1.391A1.392,1.392,0,0,0,18.548,11.592Zm-15.3,1.391a1.391,1.391,0,1,0-1.391-1.391A1.393,1.393,0,0,0,3.246,12.983ZM10.2,3.246a1.391,1.391,0,0,0,2.782.048V3.2a1.391,1.391,0,0,0-2.782.048Z" transform="translate(0.408 0.408)" fill="currentColor" />
                                    </svg>
                                </span>
                                <div className="fs-8 text-gray-800 fw-semibold">Network</div>
                            </div>
                            <button type="button" className="close_filter btn btn-icon position-absolute end-0" onClick={filterApply(filterKey)}></button>
                        </Accordion.Header>
                        <AndOrCondition condition={state.condition} conditionApply={conditionApply} />
                    </div>
                    <Accordion.Body className="border rounded-1 mt-1">
                        {state.feedData && Object.keys(state.feedData).length > 0 ?
                        <>
                        <div className="navi-item d-flex align-items-center py-2 cursor-pointer">
                            <label className="form-check flex-grow-1" htmlFor="searchNetwork">
                                <div className="navi-text">
                                    <p className="fs-8 text-gray-800 fw-semibold mb-0">Search Network</p>
                                </div>
                            </label>
                            <input className="form-check-input" type="checkbox" name="" value="phone" onClick={onClickSelectAll} checked={Object.keys(state.selectedNetwork).length >= state.networkData.length ? true : false} />
                        </div>
                        <div className="form-group mb-2">
                            <input type="text" className="form-control form-control-sm" id="searchNetwork" placeholder="Search your network..." onChange={onSearchFilter} />
                        </div>
                        <div className="">
                            {
                                state.networkData.map((item) => {
                                    return <div className="dropdown-item px-0 border-bottom rounded-0" >
                                        <label className="form-check flex-grow-1 overflow-hidden align-items-center" htmlFor={`filter_${GET_NETWORK_ICON(item.id)}`}>
                                            <span className="symbol symbol-22">
                                                <img className="img-fluid h-100" src={baseUrl(`media/icons/social-icons-square/${GET_NETWORK_ICON(item.id)}.svg`)} height={20} width={20} alt={item.name} />
                                            </span>
                                            <span className="form-check-label text-gray-800 text-ellipsis">{item.name}</span>
                                        </label>
                                        <input className="form-check-input flex-shrink-0" type="checkbox" name="" value="phone" id={`filter_${GET_NETWORK_ICON(item.id)}`} checked={Object.keys(state.selectedNetwork).includes(`${item.id}`) ? true : false} onClick={onNetworkApply(item.id)} />
                                    </div>
                                })
                            }

                        </div>
                        </>
                        :
                        <div className="py-4">
                            <h6 className="text-center fs-8 mb-0">No Networks Available</h6>
                        </div>
                        }
                    </Accordion.Body>
                </Accordion.Item>
            </Accordion>
    }
}

export default Network;