import React, { Component } from 'react';
import { Dropdown, Accordion, Nav, Tab, ToggleButtonGroup, ToggleButton, OverlayTrigger, Tooltip } from 'react-bootstrap';
import { capitalizeFirstLetter } from '../../../../../Services/Utils/utils';


class AndOrCondition extends Component {
    render() {

        const { condition,conditionApply } = this.props
        return <Dropdown align="start" className="block-center position-relative">
            <Dropdown.Toggle variant="secondary" className="arrow_disabled">
                {capitalizeFirstLetter(condition)}
            </Dropdown.Toggle>
            <Dropdown.Menu className="btn-min-w">
                <div className="menu-item">
                    <Dropdown.Item className={condition == `and` ? `active` : ``} onClick={conditionApply("and")}>And</Dropdown.Item>
                </div>
                <div className="menu-item">
                    <Dropdown.Item className={condition == `or` ? `active` : ``} onClick={conditionApply("or")}>Or</Dropdown.Item>
                </div>
            </Dropdown.Menu>
        </Dropdown>
    }
}

export default AndOrCondition