import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Dropdown, Accordion, Nav, Tab, ToggleButtonGroup, ToggleButton, OverlayTrigger, Tooltip } from 'react-bootstrap';
import { baseUrl } from '../../../../../Assets/AssetsHelper';
import { getTemplateList } from "../../../../../Services/Actions/templateAction";

const removeDuplicate = (data) => {
    try {
        var updateFilter = []
        var updateKey = {}
        data.map((item) => {
            if (!updateKey[item.id]) {
                updateFilter.push(item)
                updateKey[item.id] = true
            }

        })
        return updateFilter
    }
    catch (ex) {
        return data
    }
}

class Templates extends Component {
    state = {
        templeteList: [],
    }
    componentDidMount() {
        this.onLoad()
    }
    onLoad = () => {
        var self = this
        getTemplateList({ type: 1 }).then((Response) => {
            const { responseData } = Response.data
            self.setState({ templeteList: removeDuplicate(responseData.templates) })
        })
    }
    onFiltertemplateName = (id) => {
        const { templeteList } = this.state;

        if (templeteList && templeteList.length > 0) {
            const updated = templeteList.filter((item) => item.id == id)
            if (updated && updated.length > 0) {
                return updated[0].name
            }
            else return ""
        }
        else return ""

    }
    render() {
        const { templeteList } = this.state;
        const { onSelectTemplate, state } = this.props
        return <>
            <label className="text-gray-800 mb-2">Select Template</label>
            <Accordion.Item eventKey="8" className="pb-3">
                <Accordion.Header className="w-100">
                    <span className="fs-8 fw-medium">{state.selectedTemplate ? state.selectedTemplate.name ? state.selectedTemplate.name : this.onFiltertemplateName(state.selectedTemplate.id) : `Please select template.`}</span>
                </Accordion.Header>
                <Accordion.Body className="border rounded-1 mt-1">
                    {templeteList && templeteList.length > 0 ?
                        <ToggleButtonGroup type="radio" className="flex-column w-100 pb-2" name="options" defaultValue={0}>
                            {templeteList.map((temp, index) => {
                                return <><ToggleButton key={index} className="p-0 card rounded-1 text-decoration-none cursor-pointer mb-2" variant="outline-primary" id="tbg-radio-1" value={index} onClick={onSelectTemplate(temp)}>
                                    <div className="card-body p-2">
                                        <div className="d-flex align-items-center">
                                            <div className="symbol symbol-50 flex-shrink-0 me-3">
                                                <img className="img-fluid" src={baseUrl(`media/images/themes/ModernCard.png`)} height={50} width={50} alt="ModernCard" />
                                            </div>
                                            <div className="overflow-hidden text-start">
                                                <p className={`fs-7 fw-semibold mb-1 text-ellipsis text-gray-dark`}>{temp.name}</p>
                                                <p className="fs-8 fw-semibold mb-0 text-gray-600 text-ellipsis">55 Assets</p>
                                            </div>
                                        </div>
                                    </div>
                                </ToggleButton></>
                            })}
                        </ToggleButtonGroup>
                        : null}
                    <div className="text-center">
                        <Link to={'/template/add'} className="btn btn-sm p-0">
                            <span className="svg-icon">
                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                    <path fill="none" d="M0 0h24v24H0z" />
                                    <path d="M11 11V5h2v6h6v2h-6v6h-2v-6H5v-2z" fill="currentColor" />
                                </svg>
                            </span>
                            Create New Template
                        </Link>
                    </div>
                </Accordion.Body>
            </Accordion.Item>

        </>
    }
}

export default Templates