
import React, { Component } from 'react';
import { connect } from "react-redux";
import { Accordion } from 'react-bootstrap';
import { baseUrl } from '../../../../../Assets/AssetsHelper';
import { GET_NETWORK_ICON } from '../../../../../Services/Utils/utils';
import AndOrCondition from './AndOr'

class Connection extends Component {


    render() {
        const { state, onconnectionApply, onConnectionSelectAll, onConnectionSearchFilter, conditionApply, filterApply, filterKey, indexFilter,isLast,onSelectActive } = this.props
        console.log('state.feedData', state.feedData)
        return <Accordion activeKey={state.activeKey} onSelect={onSelectActive(filterKey)?onSelectActive(filterKey):null}>
                <Accordion.Item eventKey={'connection'} className="pb-2">
                    <div className="d-flex">
                        <Accordion.Header className="me-1">
                            <div className="d-flex flex-grow-1 align-items-center pe-4">
                                <span className="svg-icon svg-icon-4 text-gray-800">
                                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                        <path fill="none" d="M0 0H24V24H0z" />
                                        <g><path id="Path_2321" data-name="Path 2321" d="M21,14v5a2,2,0,0,1-2,2H5a2,2,0,0,1-2-2V5A2,2,0,0,1,5,3h5V5H5V19H19V14Z" fill="#313a53" />
                                            <path id="Path_2322" data-name="Path 2322" d="M21,7H17V3H15V7H11V9h4v4h2V9h4" fill="currentColor" /></g>
                                    </svg>
                                </span>
                                <div className="fs-8 text-gray-800 fw-semibold">Connections</div>
                            </div>
                            <button type="button" className="close_filter btn btn-icon position-absolute end-0" onClick={filterApply(filterKey)}></button>
                        </Accordion.Header>
                        <AndOrCondition condition={state.condition} conditionApply={conditionApply} />
                    </div>
                    <Accordion.Body className="border rounded-1 mt-1">
                        {state.feedData && Object.keys(state.feedData).length > 0 ?
                        <>
                        <div className="navi-item d-flex align-items-center py-2 cursor-pointer">
                            <label className="form-check flex-grow-1" htmlFor="searchNetwork">
                                <div className="navi-text">
                                    <p className="fs-8 text-gray-800 fw-semibold mb-0">Search Connection</p>
                                </div>
                            </label>
                            <input className="form-check-input" type="checkbox" name="" value="" onClick={onConnectionSelectAll} checked={Object.keys(state.selectedConnection).length >= state.feedData.length ? true : false} />
                        </div>
                        <div className="">
                            {
                                state.feedData.map((item) => <div className="dropdown-item px-0 border-bottom rounded-0" >
                                    <label className="form-check flex-grow-1 overflow-hidden align-items-center" htmlFor={`connection`}>
                                        <span className="symbol symbol-22">
                                            <img className="img-fluid h-100" src={baseUrl(`media/icons/social-icons-square/${GET_NETWORK_ICON(item.networkId)}.svg`)} height={20} width={20} alt={'twitter'} />
                                        </span>
                                        <span className="form-check-label text-gray-800 text-ellipsis">{item.name}</span>
                                    </label>
                                    <input className="form-check-input flex-shrink-0" type="checkbox" id={item.feedId} name={item.feedId} value={item.feedId} onClick={onconnectionApply(item.feedId)} checked={Object.keys(state.selectedConnection).includes(`${item.feedId}`) ? true : false} />
                                </div>)
                            }
                            </div>
                        </>
                        :
                        <div className="py-4">
                            <h6 className="text-center fs-8 mb-0">No Connections Available</h6>
                        </div>
                        }
                    </Accordion.Body>
                </Accordion.Item>

            </Accordion>
    }
}
export default Connection;